.SelectTrigger {
  border: 1px solid var(--Gray-Medium, #dedede);
  background: var(--White, #fff);
  color: var(--Almost-Black, #3e4849);
  cursor: pointer;
  letter-spacing: -.14px;
  border-radius: 20px;
  align-items: center;
  gap: 4px;
  height: 40px;
  padding: 0 16px;
  font-size: 14px;
